:root {
  font-size: 60%;

  --color-background: #F0F0F7;
  --color-primary-lighter: #6F91B3;
  --color-primary-light: #4F6780;
  --color-primary: #214467;
  --color-primary-dark: #22377D;
  --color-primary-darker: #18324D;
  --color-secundary: #B39B27;
  --color-secundary-dark: #BF8521;
  --color-title-in-primary: #FFFFFF;
  --color-text-in-primary: #C3DEE9;
  --color-text-title: #214467;
  --color-text-complement: #9C98A6;
  --color-text-base: #6A6180;
  --color-line-in-white: #E6E6F0;
  --color-input-background: #F8F8FC;
  --color-button-text: #FFFFFF;
  --color-box-base: #FFFFFF;
  --color-box-footer: #FAFAFC;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100vh;
}

body {
  background: var(--color-background);
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
}

body, input button, textarea {
  font: 500 1.6rem Poppins;
}

.container {
  width: 90vw;
  max-width: 700px;
}

@media(min-width: 700px) {
  :root {
    font-size: 62.5%;
  }
  
}