.page-rpv-generate {
  height: 100vh;
  width: 100vw;
}

.page-rpv-generate .page-header .header-content {
  margin-bottom: 6.4rem;
}

.page-rpv-generate main {
  background: var(--color-box-base);
  width: 100%;
  max-width: 74rem;
  border-radius: 0.8rem;
  margin: -3.2rem auto 3.2rem;
  padding-top: 6.4rem;
  overflow: hidden;
}

.page-rpv-generate main fieldset {
  border: 0;
  padding: 0 2.4rem;
}
#step-bar-progress {
  border: 0;
  padding: 0 2.4rem;
}
.page-rpv-generate main fieldset + fieldset {
  margin-top: 6.4rem;
}
.page-rpv-generate main fieldset legend {
  font: 700 2.4rem Archivo;
  color: var(--color-text-title);
  margin-bottom: 2.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 1.7rem;
  border-bottom: 1px solid var(--color-line-in-white);
}

.page-rpv-generate main fieldset .fild {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-rpv-generate main fieldset .fild label  {
  font: 700 1.4rem Archivo;
  color: var(--color-text-title);
  margin-bottom: 2.4rem;
  display: flex;
  align-items: center;
  padding-bottom: 0rem;
}
.page-rpv-generate main fieldset .fild label.result  {
  font: 2rem Archivo;
  color: var(--color-text-complement);
  margin-bottom: 2.4rem;
  display: flex;
  align-items: center;
  padding-bottom: .2rem;
  margin-left: .5rem;
}

.page-rpv-generate main fieldset legend button  {
  background: none;
  border: 0;
  color: var(--color-primary);
  font: 700 1.6rem Archivo;
  cursor: pointer;
  transition: color 0.2s;
}

.page-rpv-generate main fieldset legend button:hover {
  color: var(--color-secundary);
}

#btn-next{
  background: none;
  border: 0;
  color: var(--color-primary);
  font: 700 1.6rem Archivo;
  cursor: pointer;
  transition: color 0.2s;
}

.btn-next:hover {
  color: var(--color-secundary);
}


.page-rpv-generate main fieldset .input-block + .textarea-block,
.page-rpv-generate main fieldset .select-block + .input-block {
  margin-top: 2.4rem;
}

.page-rpv-generate main footer {
  padding: 4rem 2.4rem;
  background: var(--color-box-footer);
  border-top: 1px solid var(--color-line-in-white);
  margin-top: 6.4rem;
}

.page-rpv-generate main footer p {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: var(--color-text-complement);
}

.page-rpv-generate main footer p img{
  margin-right: 2rem;
}

.page-rpv-generate .warn {
  display: flex;
  align-items: left;
  padding: 4rem 2.4rem;
  background: var(--color-box-footer);
}
.page-rpv-generate .warn p {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: var(--color-text-complement);
}

.page-rpv-generate .warn img{
  margin-right: 2rem;
}
.page-rpv-generate main footer button {
  width: 100%;
  height: 5.6rem;
  background: var(--color-primary);
  color: var(--color-button-text);
  border: 0;
  border-radius: 0.8rem;
  cursor: pointer;
  font: 700 1.6rem Archivo;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: background-color 0.2s;
  margin-top: 3.2rem;
}

.page-rpv-generate main footer button:hover {
  background: var(--color-secundary);
}


#btn-next {
  width: 100%;
  height: 5.6rem;
  background: var(--color-primary);
  color: var(--color-button-text);
  border: 0;
  border-radius: 0.8rem;
  cursor: pointer;
  font: 700 1.6rem Archivo;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: background-color 0.2s;
  margin-top: 3.2rem;
}

#btn-next:hover {
  background: var(--color-secundary);
}

@media(min-width: 700px) {
  .page-rpv-generate {
    max-width: 100vw;
  }

  .page-rpv-generate .page-header .header-content {
    margin-bottom: 0;
  }

  .page-rpv-generate main fieldset {
    padding: 0 6.4rem;
  }
  #step-bar-progress {
    padding: 0 6.4rem;
    margin-bottom: 6rem;
  }
  .page-rpv-generate main .steps {
    padding: 0 6.4rem;
  }

  .page-rpv-generate main fieldset  .block-fees  {
    display: grid;
    grid-template-columns: 4fr 2fr;
    column-gap: 1.6rem;
    
  }
  .input-block .input-value{
    border-radius: 0 0.8rem 0.8rem 0;
  }
  
  .input-block #prefix {
    width:6rem;
    border-radius: 0.8rem 0 0 0.8rem;
    color: var(--color-text-complement);
  }
  .input-block .input-currency{
    display: flex;
    flex-direction: row;
  }
  .page-rpv-generate main fieldset .block-fees .input-block {
    margin-top: 0;
  }

  .page-rpv-generate main fieldset  .initial-fees  {
    display: grid;
    grid-template-columns: 4fr 2fr;
    column-gap: 1.6rem;
    margin-top: 2.4rem;
  }

  .page-rpv-generate main fieldset .initial-fees .input-block {
    margin-top: 0;
  }
  #block-benefits  {
    margin-top: 2.4rem;
    display: grid;
    grid-template-columns: 2fr 2fr;
    column-gap: 1.6rem;
  }

  #block-benefits .input-block{
    margin-top: 0;
  }
  .page-rpv-generate main fieldset  .block-benefits-percent  {
    margin-top: 2.4rem;
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    column-gap: 1.6rem;
  }

  .page-rpv-generate main fieldset  .block-benefits-percent .input-block{
    margin-top: 0;
  }
  .page-rpv-generate main .schedule-item {
    display: grid;
    grid-template-columns: 3fr 0.8fr 2fr;
    column-gap: 1.6rem;
  }

  .page-rpv-generate main .schedule-item .input-block,
  .page-rpv-generate main .schedule-item .select-block{
    margin-top: 0;
  }
  .page-rpv-generate main fieldset .schedule-item .select-block + .input-block {
    margin-top: 0;
  }

  .page-rpv-generate main footer {
    padding: 4.0rem 6.4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .page-rpv-generate .warn {
    padding: 0rem 6.4rem 2rem 6.4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .page-rpv-generate main footer p {
    justify-content: space-between;
  }

  .page-rpv-generate main footer button {
    max-width: 20rem;
    margin-top: 0;
  }

  #btn-next {
    max-width: 20rem;
    margin-top: 0;
  }
  .input-radio {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 1.6rem;
  }
#percent {
  height: 5rem;
    border: 0px;
    width: 100%;
}

  .input-radio .radio-option {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .input-radio .radio-option label {
    font-size: 1.4rem;
    color: var(--color-text-complement);
  }
  body {
    background: #F0F0F7;
  }
}