#page-landing  {
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  background: var(--color-backgrownd);
}

.lady-container {
  text-align: center;
}

.lady-image {
  width: 60%;
  margin-left: 6.5rem;
}

.logo-container img {
  height: 15rem;
  margin-bottom: 3.5rem;

}
.logo-container {
  height: 15rem;
  text-align: center;
}

.buttons-container {
  display: flex;
  justify-content: center;
  margin: 5rem 0;
}

.buttons-container a {
  width: 30rem;
  height: 10.4rem;
  border-radius: 0.8rem;
  
  font: 700 2.0rem Archivo;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-decoration: none;
  color: var(--color-button-text);
}
.buttons-container a img {
  margin-bottom: 1.6rem;
}

.buttons-container a:first-child {
  margin-right: 1.6rem;
}

.iconbuttom {
  height: 4rem;
}

.buttons-container .gerarRPV {
  background: var(--color-primary);
}

.buttons-container .buscarRPV {
  background: var(--color-primary);
}

.buttons-container a{
  transition: 0.2s;
}

.buttons-container a:hover{
  background: var(--color-secundary);
}

@media(min-width:1100px){
  #page-landing-content {
    max-width: 1100px;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 2fr 2fr;
    grid-template-areas: 
      "lady logo"
      "lady buttons"
    ;
  }
  .logo-container img {
    height: 25rem;
    margin-bottom: 3.5rem;
    grid-area: logo;
  
  }
  .logo-container {
    height: 25rem;
    text-align: center;
  }
  
  .lady-container {
    grid-area: lady;
  }
  .lady-image {
    width: 100%;
    margin-left: 6.5rem;
  }
  .buttons-container {
    margin-top: 8rem;
    grid-area: buttons;
  }
  .buttons-container a {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .buttons-container a img {
    margin-right: 2rem;
  }
  .logo-container {
    margin-top: 5rem;
  }
  .logo-container img{
    height: 90%;
  }

}